.select__option.active:after {
  content: '';
  position: absolute;
  top: 16px;
  right: 21px;
  width: 16px;
  height: 8px;
  border: solid #1B47BF;
  border-width: 0 0 4px 4px;
  transform: rotate(-45deg);
  transition: border-color;
}

