@mixin d {
  @media only screen and (max-width: "1120px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@mixin f {
  font-family: "Roboto", sans-serif; }
@mixin bg {
  background: linear-gradient(110.27deg, rgba(255, 255, 255, 0.08) 0%, rgba(255, 255, 255, 0) 74.43%), linear-gradient(180deg, #1144C1 0%, #002BB1 100%); }
@mixin skew {
  transform: skewX(-36deg); }

$cl: #3A3F48;
$blue: #1B47BF;

body {
  min-width: 1260px;
  background: #E5E5E5;
  @include f;
  font-size: 14px;
  line-height: 1.3;
  &.no-scroll {
    overflow: hidden; } }
button {
  background: none; }
a {
  color: #316AFE;
  text-decoration: none; }

.page {
  display: flex;
  min-height: 100vh;
  flex-direction: column; }

.container {
  flex: 1 1 auto; }

.center {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 30px; }

.hero {
  position: relative;
  margin-bottom: -40px;
  background-color: #2C3038;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  z-index: 2;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(black,.2);
    z-index: -2; }
  &__top,
  &__links,
  &__link,
  &__socials {
    display: flex;
    align-items: center; }
  &__top {
    position: relative;
    height: 48px;
    padding: 0 48px;
    z-index: 2;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 18px;
      right: 18px;
      height: 100%;
      background: white;
      @include skew;
      z-index: -2; } }
  &__link {
    margin-right: 40px;
    font-size: 14px;
    font-weight: 700;
    color: $cl; }
  &__link &__pic {
    margin-right: 10px; }
  &__socials {
    margin-left: auto; }
  &__container {
    display: flex;
    min-height: 392px;
    padding: 40px 0 90px; }
  &__title {
    margin: auto;
    font-size: 48px;
    font-weight: 700;
    color: white;
    line-height: (56/48); } }

.btn {
  display: inline-block;
  position: relative;
  min-width: 144px;
  padding: 11px 32px;
  background: none;
  font-size: 16px;
  font-weight: 900;
  color: white;
  text-align: center;
  transition: opacity .2s;
  line-height: 1.15;
  z-index: 2;
  &:hover {
    opacity: .8; }
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 15px;
    right: 15px;
    height: 100%;
    @include bg;
    @include skew;
    z-index: -2; }
  &_white {
    color: #35497C;
    &:before {
      background: white; } }
  &-stroke {
    padding: 9px 20px 8px;
    border: 4px solid $cl;
    border-radius: 19px;
    font-size: 12px;
    font-weight: 900;
    color: $cl;
    text-transform: uppercase;
    transition: opacity;
    &:hover {
      opacity: .8; } } }

.header {
  position: relative;
  z-index: 10;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: -40px;
    right: calc(50% - 598px);
    height: 100%;
    @include bg;
    @include skew;
    z-index: -2; }
  &__center {
    display: flex;
    min-height: 80px;
    align-items: center; }
  &__logo {
    margin-right: auto; }
  &__pic {
    display: block; }
  &__nav {
    display: flex;
    height: 80px;
    margin-right: 30px;
    line-height: 80px; }
  &__link {
    font-size: 16px;
    color: #AABBE5;
    font-weight: 700;
    transition: box-shadow .2s, color .2s;
    @include nl {
      margin-right: 44px; }
    &:hover {
      color: white; }
    &.active {
      box-shadow: 0 4px 0 0 white inset;
      color: white; } }
  &__btn {
    margin-right: -15px; } }

.shape {
  position: absolute;
  top: 0;
  left: calc(50% - 120px);
  right: -210px;
  height: 576px;
  background: $cl;
  transform: skewX(36deg);
  overflow: hidden;
  z-index: -2;
  &__bg {
    position: absolute;
    top: 0;
    left: -210px;
    right: -210px;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    @include skew; }
  &_left {
    left: auto;
    right: calc(50% - 150px);
    left: -210px;
    transform: skewX(-36deg); }
  &_left &__bg {
    transform: skewX(36deg); } }

.title {
  margin-bottom: 16px;
  font-size: 24px;
  font-weight: 900;
  color: #35497C;
  line-height: (28/24);
  text-align: center;
  text-transform: uppercase; }

.content {
  font-size: 16px;
  color: rgba(#4E4E4E,.9);
  line-height: (26/16); }

.about {
  position: relative;
  margin-top: -40px;
  padding: 80px 0;
  overflow: hidden;
  &__row {
    display: flex;
    margin: 0 -18px; }
  &__col {
    padding: 0 18px;
    flex: 0 0 50%;
    &:last-child {
      padding-top: 276px; } }
  &__card {
    padding: 114px 56px 106px;
    background: white;
    text-align: center; }
  &__preview {
    margin-bottom: 32px;
    font-size: 0; }
  &__more {
    margin-top: 76px; }
  &__text {
    max-width: 440px;
    margin-bottom: 30px;
    color: #35497C;
    font-weight: 700;
    line-height: (23/14); } }

.quote {
  position: relative;
  overflow: hidden;
  &__center {
    display: flex;
    height: 576px;
    align-items: center;
    justify-content: flex-end; }
  &__content {
    position: relative;
    max-width: 880px;
    padding: 66px 200px 62px 240px;
    font-size: 20px;
    font-style: italic;
    font-weight: 700;
    color: white;
    line-height: (33/20);
    z-index: 2;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 110px;
      right: 110px;
      height: 100%;
      @include bg;
      @include skew;
      z-index: -2; }
    &:after {
      content: '';
      position: absolute;
      top: 35px;
      left: 200px;
      width: 140px;
      height: 102px;
      background: url(../img/quote.svg) no-repeat center / contain;
      z-index: -1; } } }

.masters {
  padding-top: 80px;
  &__heading {
    margin-bottom: 80px;
    font-weight: 700;
    text-transform: uppercase; }
  &__row {
    display: flex;
    margin: 0 -18px;
    justify-content: center;
    flex-wrap: wrap; }
  &__col {
    margin: 0 18px 80px;
    background: white;
    text-align: center;
    flex: 0 0 calc(50% - 36px); }
  &__preview {
    display: block;
    background: #262C35;
    overflow: hidden; }
  &__preview:hover &__pic {
    transform: scale(1.05); }
  &__pic {
    display: block;
    width: 100%;
    transition: transform .4s; }
  &__wrap {
    padding: 56px 56px 50px; }
  &__title {
    display: inline-block;
    text-transform: none;
    transition: opacity .2s;
    &:hover {
      opacity: .7; } } }

.footer {
  padding: 44px 0 52px;
  @include bg;
  &__row {
    display: flex; }
  &__col {
    margin-right: 100px;
    &:last-child {
      margin: 0 0 0 auto; } }
  &__logo {
    display: block;
    margin-bottom: 12px; }
  &__pic {
    display: block; }
  &__content {
    font-size: 16px;
    color: white;
    line-height: 1.64;
    a {
      color: white; } }
  &__nav {
    display: flex;
    margin-bottom: 108px; }
  &__link {
    font-size: 16px;
    font-weight: 500;
    color: white;
    opacity: .64;
    transition: opacity .2s;
    &:not(:last-child) {
      margin-right: 44px; }
    &:hover {
      opacity: 1; } }
  &__socials {
    display: flex;
    justify-content: flex-end; } }

.field {
  &__input,
  &__area {
    width: 100%;
    background: #FCFCFC;
    border: 2px solid #EFEFEF;
    border-radius: 4px;
    @include f;
    font-size: 14px;
    font-weight: 500;
    color: #35497C;
    &::placeholder {
      color: rgba(#35497C,.6); } }
  &__input {
    height: 48px;
    padding: 0 18px; }
  &__area {
    height: 198px;
    padding: 14px 18px;
    &_sm {
      height: 122px; } } }

.select {
  position: relative;
  font-weight: 500;
  text-align: left;
  &__head {
    height: 48px;
    padding: 0 18px;
    background-color: #FCFCFC;
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
    background-size: 20px 20px;
    background-position: calc(100% - 18px) 50%;
    border: 2px solid #EFEFEF;
    border-radius: 4px;
    font-size: 14px;
    color: rgba(#35497C,.6);
    line-height: 48px;
    cursor: pointer; }
  &__dropdown {
    display: none;
    position: absolute;
    top: calc(100% - 2px);
    left: 0;
    width: 100%;
    max-height: 136px;
    background: #FCFCFC;
    border: 2px solid #EFEFEF;
    border-radius: 0 0 4px 4px;
    overflow: auto; }
  &__option {
    display: block;
    position: relative;
    padding: 15px 18px;
    color: rgba(#35497C,.6);
    transition: background .2s, color .2s;
    &:hover {
      background: $blue;
      color: white; }
    &.active {
      color: $blue;
      &:after {
        content: '';
        position: absolute;
        top: 16px;
        right: 21px;
        width: 16px;
        height: 8px;
        border: solid $blue;
        border-width: 0 0 4px 4px;
        transform: rotate(-45deg);
        transition: border-color; }
      &:hover {
        color: white;
        &:after {
          border-color: white; } } } }
  &.open {
    z-index: 60; }
  &.open &__head {
    border-radius: 4px 4px 0 0; }
  &.open &__dropdown {
    display: block; }
  &.selected &__head {
    color: #35497C; } }

.form {
  padding: 104px 0 100px;
  text-align: center;
  &__container {
    position: relative;
    max-width: 800px;
    margin: 0 auto;
    padding: 64px 200px 0;
    background: white;
    box-shadow: 0px 59px 122px rgba(0, 0, 0, 0.042);
    border-radius: 16px; }
  &__preview {
    margin-bottom: 24px;
    font-size: 0; }
  &__title {
    margin-bottom: 48px; }
  &__subtitle {
    margin-bottom: 16px;
    font-size: 24px;
    font-weight: 700;
    color: #35497C; }
  &__row {
    display: flex;
    margin: 0 -12px; }
  &__col {
    padding: 0 12px;
    flex: 0 0 50%;
    &_lg {
      flex: 1 1 auto; }
    &_auto {
      flex: initial; } }
  &__field {
    margin-bottom: 24px; }
  &__action {
    margin-top: 48px;
    font-weight: 700; }
  &__checkbox {
    margin-top: 7px; }
  &__content {
    margin: 0 -20px;
    font-size: 16px;
    color: rgba(#4E4E4E,.9);
    line-height: (26/16);
    a {
      text-decoration: underline;
      &:hover {
        text-decoration: none; } } }
  &__code {
    margin-top: 40px;
    font-size: 24px;
    font-weight: 700;
    line-height: (75/64);
    color: #F66; }
  &__foot {
    position: relative;
    top: 19px;
    margin-top: 45px; }
  &_modal {
    display: none;
    padding: 0 0 19px; }
  &_profile {
    padding: 0;
    text-align: left; } }

.dashboard {
  padding: 42px 0 60px;
  &__head,
  &__nav {
    display: flex;
    align-items: center; }
  &__head {
    min-height: 40px;
    margin-bottom: 32px; }
  &__nav {
    margin-right: auto; }
  &__link {
    margin-right: 32px;
    padding: 6px 0;
    border-bottom: 4px solid transparent;
    font-size: 16px;
    font-weight: 500;
    color: #6e7c9e;
    transition: border-color .2s, color .2s;
    &.active {
      border-color: #1543BE;
      color: #1543BE; } }
  &__add {
    font-size: 0;
    transition: opacity .2s;
    &:hover {
      opacity: .8; } } }

.checkbox {
  display: block;
  position: relative;
  min-width: 32px;
  min-height: 32px;
  cursor: pointer;
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0; }
  &__input:checked + &__content {
    &:before {
      border-color: $blue; }
    &:after {
      opacity: 1; } }
  &__content {
    display: block;
    &:not(:empty) {
      padding: 7px 0 7px 40px;
      font-size: 14px;
      color: rgba(#35497C,.6); }
    &:before,
    &:after {
      content: '';
      position: absolute; }
    &:before {
      top: 0;
      left: 0;
      width: 32px;
      height: 32px;
      border: 4px solid #E2E2E2;
      border-radius: 4px;
      transition: border-color .2s; }
    &:after {
      top: 10px;
      left: 8px;
      width: 16px;
      height: 8px;
      border: solid $blue;
      border-width: 0 0 4px 4px;
      opacity: 0;
      transform: rotate(-45deg);
      transition: opacity .2s; } } }

.users {
  &__item {
    display: flex;
    margin-bottom: 2px; }
  &__cell {
    display: flex;
    padding: 20px;
    background: white;
    align-items: center;
    &:first-child {
      flex: 0 0 342px; }
    &:nth-child(2) {
      flex: 1 1 auto; }
    &:not(:first-child) {
      margin-left: 2px; } }
  &__preview {
    margin-right: 16px;
    flex: 0 0 48px; }
  &__pic {
    display: block;
    max-width: 100%;
    border-radius: 7px; }
  &__name {
    font-size: 18px;
    font-weight: 700;
    color: $cl; }
  &__code {
    color: #FC6769; }
  &__info {
    margin-top: 2px;
    font-size: 14px;
    font-weight: 500;
    color: rgba(#35497C,.8);
    &_orange {
      color: rgba(#FFA800,.8); }
    &_red {
      color: rgba(#FF3C3C,.8); } }
  &__scale {
    position: relative;
    width: 100%;
    height: 38px;
    border-radius: 19px;
    background: #D2D9E9; }
  &__progress {
    height: 100%;
    @include bg;
    border-radius: 19px; }
  &__counter {
    position: absolute;
    top: 10px;
    left: 16px;
    font-size: 14px;
    font-weight: 500;
    color: white; }
  &__checkbox {
    margin: 0 auto; }
  &_single {
    display: flex;
    align-items: center; } }

.fancybox-content {
  background: none; }

.attendance {
  width: 504px;
  padding: 46px 40px;
  background: #EFEFEF;
  box-shadow: 0px 59px 122px rgba(0, 0, 0, 0.042);
  border-radius: 16px;
  &__title {
    margin-bottom: 32px;
    font-size: 24px;
    font-weight: 900;
    color: #35497C; } }

.profile {
  width: 1000px;
  padding: 40px 0;
  background: white;
  border-radius: 16px;
  &__row {
    display: flex; }
  &__col {
    padding: 0 40px;
    flex: 0 0 50%;
    &:first-child {
      border-right: 1px solid rgba(#C4C4C4,.25); } }
  &__section {
    &:not(:last-child) {
      margin-bottom: 40px;
      padding-bottom: 40px;
      border-bottom: 1px solid rgba(#C4C4C4,.25); } }
  &__users {
    margin-bottom: 35px; }
  &__form {
    padding: 0; }
  &__title {
    margin-bottom: 24px;
    font-size: 18px;
    font-weight: 700; } }
